




























.init-wrapper {
    max-width: 800px;
    width: 100%;
    background: #fff;
    height: 200px;
    margin: 20vh auto 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .btn {
        margin-top: 20px;
        width: 200px;
        height: 50px;
        cursor: pointer;
        border-radius: 3px;
        background: #000;
        color: #fff;
    }
}
